@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,400&display=swap);
*{box-sizing:border-box;margin:0px;padding:0px}*:focus{outline:none}body{font-family:'Montserrat', sans-serif;padding-top:65px;position:relative}.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

header{position:fixed;top:0;left:0;width:100%;height:55px;background:#6c00da;background:linear-gradient(135deg, #6c00da 25%, #fc6900 79%);color:#fff;display:flex;justify-content:center;align-items:center}header h3{font-weight:300;letter-spacing:0.2rem}.menu-btn{position:fixed;left:15px;top:15px;width:25px;height:25px;z-index:250;background:transparent;border:none;display:flex;flex-direction:column}.menu-btn.btn-close .menu-line{transition:0.3s}.menu-btn.btn-close .menu-line:nth-child(1){position:fixed;top:25px;transform:rotate(45deg)}.menu-btn.btn-close .menu-line:nth-child(2){opacity:0}.menu-btn.btn-close .menu-line:nth-child(3){position:fixed;top:25px;transform:rotate(-45deg)}.menu-btn.btn-open .menu-line{transition:0.3s}.menu-btn .menu-line{width:25px;height:2px;border-radius:2px;padding:1px;background-color:#fff;margin:auto 0px}

.menu-list{position:fixed;top:0;background:rgba(108,0,218,0.85);background:linear-gradient(135deg, rgba(108,0,218,0.85) 25%, rgba(252,105,0,0.85) 79%);color:#fff;width:85vw;max-width:350px;height:100vh;min-height:100vh;z-index:1;transition:0.5s;display:flex;flex-direction:column;padding:55px 5px 25px}.menu-list a{width:100%;padding:10px;text-align:center;text-decoration:none;background:rgba(0,0,0,0.35);color:#fff;margin:0px 0px 5px;font-size:0.8rem;font-weight:700}.menu-list.open{animation:menuTransition 0.5s;left:0px}.menu-list.closed{left:-85vw;animation:menuTransition 0.5s reverse}@keyframes menuTransition{from{transform:translateX(-95vw)}to{transform:translateX(0)}}

.article-container{width:90%;margin:15px auto 25px;max-width:350px}.article-container img{width:100%;max-width:350px;margin:0px auto 10px}.article-container h4{line-height:1.5rem;font-size:1.1rem}.article-container p{font-size:0.8rem;font-weight:300;padding:5px 0px}

